webpackJsonp([201561590060799],{

/***/ 1042:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>I had the pleasure of spending this weekend with Ndesanjo Macha (<a href=\"http://digitalafrica.blogspot.com/\">English Blog</a>/<a href=\"http://jikomboe.blogspot.com/\">Kiswahili Blog</a>/<a href=\"http://cyber.law.harvard.edu/globalvoices/?p=132\">Profile at Global Voices</a>) in Greensboro, N.C., about an hour from my house in the woods near Pittsboro. We're working together on a Kiswahili translation of WordPress, the excellent open source platform for this website. </p>\n<p>We are fortunate to have the aid of the great invisible WordPress translator's community, which has collaboratively published at least a couple (<a href=\"http://codex.xwd.jp/index.php/Localizing_WordPress\">1</a> <a href=\"http://codex.wordpress.org/Translating_WordPress\">2</a>) of essential how-to's. Also there is of course the PoEdit team (mostly programmer <a href=\"http://www.poedit.org/donations.php\">Vaclav Slavik</a>, I think), which makes the software that edits the software. Do you guys sell T-shirts, or what? </p>\n<h3>1 Geek + 1 Native Speaker = Native-Language Software for Millions. </h3>\n<p>Any techno- or translation-minded folks please email chris at nonprofitdesign dot org. Help translate your favorite Open Source software into a new language. </p>\n<p>I'm hoping we'll be finished with it pretty soon, and we'll be promoting it to the 30-80 million estimated Kiswahili-speakers in the world. (Mostly in Kenya, Tanz. and Uganda: here's a <a href=\"http://en.wikipedia.org/wiki/Swahili\">wikipedia article </a>about Swahili.) </p>\n<p>In the meanwhile, I've transferred him to a shiny <a href=\"http://wordpress.com\">Wordpress 2.0</a> blog. It's embargoed for now, but his new domain will resolve in a few days. He's excited about WordPress because Google's Blogger, while being terrifically easy to use — is so limited a platform that it hurts to use. (Seriously, Google: you don't have <em>categories</em>, for god's sake. That's so 2005. The workaround <a href=\"http://oldcola.blogspot.com/goodies/BloggerCategories.html\">hacks </a>are hideous. ) And Blogger certainly does not have the same blogger-developer community. </p>\n<p>Ndesanjo has been using Blogger with great success in Tanzania for some time now, and he says that he sees new Tanzanian bloggers every day. (However, he sent me a link to a new Tanzanian <a href=\"http://blog.co.tz/login.php\">blog portal</a>. I signed up and found an extremely small user base‚ writing in English.) But most of the Tanzanian bloggers stick to good-ole Blogger and have had to hack their templates a bit to get key phrases into Swahili. </p>\n<p>Yikes. Who wants to try to figure out a content management system template on a dial up connection? With no documentation? No thanks. That's where we come in with the translation. </p>\n<p>Ndesanjo recently described the Swahili blogosphere in this way: </p>\n<blockquote class=\"large\"> is nearly impossible to translate most of the poems and convey the same message. Most of the time these poet bloggers are challenging each other with creative use of complex and deep Swahili, which is an established tradition in Swahili poetry.</blockquote>\n<p>So keep your aggregators tuned for developments on that front. You can read Ndesanjo's regular <a href=\"http://cyber.law.harvard.edu/globalvoices/-/world/sub-saharan-africa/tanzania/\">summaries of the Swahili blogosphere at Global Voices</a>. </p>","frontmatter":{"created_at":"January 23, 2006","path":"/2006/01/23/decolonizing-cyberspace","title":"\"Decolonizing Cyberspace\"","tags":["colonialism","translation"]}}},"pathContext":{}}

/***/ })

});


